import { Box } from "@mui/material";
import { useRouter } from "next/router";
import { useProxyWatch } from "@fanfanlo";
import { user } from "@app/model";
import toast from "react-hot-toast";
import { SignUp } from "../../sign-up/content/SignUp";

export function SignInUpContent(){
    const router = useRouter();
    const [token] = useProxyWatch(user, 'data.storeData.user_token', user.data.storeData.user_token);
    console.log('token222222=', token)
    if(token){
        const waitTime = 3000;
        toast('登录成功', { duration: waitTime })
        setTimeout(() => {
            back()
        }, waitTime + 500);
    }
    function back(){
        let p = '/';
        if(router.query.back){
            if(router.query.back instanceof Array){
                p = router.query.back[0]
            }else{
                p = router.query.back
            }
        }
        router.replace(p);
    }

    return (
        <Box>
            <SignUp />
        </Box>
    )
}