import { IRequestParams } from "@fanfanlo";
import { request } from "@fanfanlo";
import { tms } from "../tms";
import { IResultContent } from "@fanfanlo";
import { IRequestOptions } from "../IRequestOptions";

export interface IRegCodeInfo{
    login_name:string
    endtime:string
    code:string
}
export interface ITMGetVerificationCodeResult extends IResultContent{
    reg_code_info:IRegCodeInfo
}
export enum TMGetVerificationCodeType {
    cnMobile = 'cnMobile',
    email = "email"
}
interface ITMGetVerificationCodeParams extends IRequestParams{
    login_name:string
    type:TMGetVerificationCodeType
}

export async function tmsGetVerificationCode(opts:IRequestOptions<ITMGetVerificationCodeParams>){
    const res = await request<ITMGetVerificationCodeResult>(tms.TUser, tms.MUserCreateRegCode, opts.params, opts.gateway, opts.handler);
    return res
}
