import { useTranslation } from "@fanfanlo";
import { useState, useEffect, useCallback } from "react";
import { TextField, Button, Box, Typography, Stack } from "@mui/material";
import { tmsGetVerificationCode, TMGetVerificationCodeType, user } from "@app/model";
import { tmsLoginByCode, UserLoginType } from "@app/model";
import _ from "lodash";
import toast from "react-hot-toast";

const PHONE_REGEX = /^1[3-9]\d{9}$/;
const VERIFICATION_CODE_REGEX = /^\d{6}$/;

interface CountdownProps {
  seconds: number;
  onEnd: () => void;
}

function Countdown({ seconds, onEnd }: CountdownProps) {
  const [count, setCount] = useState(seconds);
  const { t } = useTranslation('homepage/components/page/sign-up/mobile/content');

  useEffect(() => {
    if (count <= 0) {
      onEnd();
      return;
    }

    const timer = setTimeout(() => {
      setCount(count - 1);
    }, 1000);

    return () => clearTimeout(timer);
  }, [count, onEnd]);

  return <span>{t('SignUpByMobile.countdown', { seconds: count })}</span>;
}

function PhoneInput({
  value,
  onChange,
  disabled = false
}: {
  value: string;
  onChange: (value: string) => void;
  disabled?: boolean;
}) {
  const { t } = useTranslation('homepage/components/page/sign-up/mobile/content');

  return (
    <TextField
      fullWidth
      label={t('SignUpByMobile.phoneNumber')}
      value={value}
      onChange={(e) => onChange(e.target.value)}
      placeholder={t('SignUpByMobile.phoneNumberPlaceholder')}
      disabled={disabled}
      margin="normal"
      inputProps={{ maxLength: 11 }}
    />
  );
}

function VerificationCodeInput({
  value,
  onChange,
  disabled = false
}: {
  value: string;
  onChange: (value: string) => void;
  disabled?: boolean;
}) {
  const { t } = useTranslation('homepage/components/page/sign-up/mobile/content');

  return (
    <TextField
      fullWidth
      label={t('SignUpByMobile.verificationCode')}
      value={value}
      onChange={(e) => {
        const value = e.target.value.replace(/\D/g, '').substring(0, 6);
        onChange(value);
      }}
      placeholder={t('SignUpByMobile.verificationCodePlaceholder')}
      disabled={disabled}
      margin="normal"
      inputProps={{ maxLength: 6 }}
    />
  );
}

function GetCodeButton({
  onClick,
  disabled = false,
  countdown = 0
}: {
  onClick: () => void;
  disabled?: boolean;
  countdown?: number;
}) {
  const { t } = useTranslation('homepage/components/page/sign-up/mobile/content');

  if (countdown > 0) {
    return (
      <Button variant="outlined" disabled fullWidth>
        <Countdown
          seconds={countdown}
          onEnd={() => { }}
        />
      </Button>
    );
  }

  return (
    <Button
      variant="outlined"
      onClick={onClick}
      disabled={disabled}
      fullWidth
    >
      {t('SignUpByMobile.getVerificationCode')}
    </Button>
  );
}

function RegisterButton({
  onClick,
  disabled = false
}: {
  onClick: () => void;
  disabled?: boolean;
}) {
  const { t } = useTranslation('homepage/components/page/sign-up/mobile/content');

  return (
    <Button
      variant="contained"
      onClick={onClick}
      disabled={disabled}
      fullWidth
      sx={{ mt: 2 }}
    >
      {t('SignUpByMobile.register')}
    </Button>
  );
}

export function SignUpByMobile() {
  const { t } = useTranslation('homepage/components/page/sign-up/mobile/content');
  const [phone, setPhone] = useState('');
  const [code, setCode] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const [countdown, setCountdown] = useState(0);
  const [error, setError] = useState('');
  const [isCodeSent, setIsCodeSent] = useState(false);
  const [isPhoneVerified, setIsPhoneVerified] = useState(false);

  const isPhoneValid = PHONE_REGEX.test(phone);
  const isCodeValid = VERIFICATION_CODE_REGEX.test(code);
  const canGetCode = !isLoading && isPhoneValid && countdown === 0;
  const canRegister = isPhoneValid && isCodeValid && !isLoading;

  const handleGetCode = useCallback(async () => {
    if (!isPhoneValid || isLoading || countdown > 0) return;

    setIsLoading(true);
    setError('');

    try {
      const res = await tmsGetVerificationCode({
        params: {
          login_name: phone,
          type: TMGetVerificationCodeType.cnMobile,
        },
        gateway: '',
        handler: { printLog: false }
      });

      if (res.err) {
        setError(res.err.toString() || t('SignUpByMobile.errors.verificationFailed'));
        return;
      }

      if (!res.shellIsOk) {
        setError(t('SignUpByMobile.errors.requestFailed'));
        return;
      }

      if (!res.dataIsOk) {
        setError(t('SignUpByMobile.errors.verificationFailed'));
        return;
      }

      // Update phone number if server returned a different one
      if (res.data?.reg_code_info?.login_name && res.data.reg_code_info.login_name !== phone) {
        setPhone(res.data.reg_code_info.login_name);
      }
      const code = res.data?.reg_code_info?.code;
      if (code && code.length == 6 && !_.isNaN(parseInt(code))) {
        setCode(code)
        toast('内部版本不需要发送短信，验证码已自动填入，点击登录即可。', { duration: 5000 })
      }

      // Start countdown
      setCountdown(60);
      setIsCodeSent(true);

    } catch (err) {
      setError(t('SignUpByMobile.errors.requestFailed'));
      console.error('Failed to get verification code:', err);
    } finally {
      setIsLoading(false);
    }
  }, [phone, isPhoneValid, isLoading, countdown, t]);

  const handleRegister = useCallback(async () => {
    if (!canRegister) return;

    setIsLoading(true);
    setError('');

    try {
      const res = await tmsLoginByCode({
        params: {
          login_name: phone,
          code,
          type: UserLoginType.cnMobile,
        },
        gateway: '',
        handler: { printLog: false }
      });

      if (res.err) {
        setError(res.err.toString() || t('SignUpByMobile.errors.loginFailed'));
        return;
      }

      if (!res.shellIsOk) {
        setError(t('SignUpByMobile.errors.requestFailed'));
        return;
      }

      if (!res.dataIsOk || !res.data?.is_ok) {
        setError(res.data?.msg || t('SignUpByMobile.errors.loginFailed'));
        return;
      }

      const user_token = res.data?.user_token
      if (user_token) {
        user.data.storeData.user_token = user_token 
        console.log('Registration successful:', res.data);
      } else {
        setError(res.data?.msg || t('SignUpByMobile.errors.loginFailed'));
        return;
      }

    } catch (err) {
      setError(t('SignUpByMobile.errors.requestFailed'));
      console.error('Registration failed:', err);
    } finally {
      setIsLoading(false);
    }
  }, [phone, code, canRegister, t]);

  // Handle countdown end
  useEffect(() => {
    if (countdown > 0) {
      const timer = setTimeout(() => setCountdown(countdown - 1), 1000);
      return () => clearTimeout(timer);
    }
  }, [countdown]);

  return (
    <Box sx={{ maxWidth: 400, mx: 'auto', p: 3 }}>
      <Typography variant="h5" component="h1" gutterBottom align="center">
        {t('SignUpByMobile.title')}
      </Typography>

      <form onSubmit={(e) => {
        e.preventDefault();
        if (canRegister) handleRegister();
      }}>
        <Stack spacing={2}>
          <PhoneInput
            value={phone}
            onChange={setPhone}
            disabled={isLoading || countdown > 0}
          />

          <Box sx={{ display: 'flex', gap: 2 }}>
            <VerificationCodeInput
              value={code}
              onChange={setCode}
              disabled={isLoading || !isCodeSent}
            />
            <GetCodeButton
              onClick={handleGetCode}
              disabled={!canGetCode}
              countdown={countdown}
            />
          </Box>

          <RegisterButton
            onClick={handleRegister}
            disabled={!canRegister || isLoading}
          />

          {error && (
            <Typography color="error" align="center">
              {error}
            </Typography>
          )}
        </Stack>
      </form>
    </Box>
  );
}
