import { IRequestParams, IResultContent } from "@fanfanlo"
import { UserLoginType } from "./common"
import { request } from "@fanfanlo"
import { tms } from "../tms"
import { IRequestOptions } from "../IRequestOptions";


export interface ITMLoginByCodeParams extends IRequestParams{
    login_name:string
    code:string
    type:UserLoginType
}


export interface ITMLoginByCodeResult extends IResultContent{
    is_ok:boolean
    msg:string
    user_token?:string
}

export async function tmsLoginByCode(opts:IRequestOptions<ITMLoginByCodeParams>){
    const res = await request<ITMLoginByCodeResult>(tms.TUser, tms.MUserLoginByCode, opts.params, opts.gateway, opts.handler);
    return res
}
